<template>
  <div class="back">
    <el-container>
        <el-header style="display:flex;flex-direction:column;justify-content: center;margin-top: 25px;margin-bottom: 5px;" >
            <el-row justify="center"  align="middle">
                <el-col :span="2" @click="next">
                    <el-image :src="require('@/assets/p3/p3-tanup.png')" :fit="cover" />
                </el-col>
            </el-row>
            <el-row justify="center"  align="middle">
                <el-col :span="2" @click="pre" >
                    <el-image :src="require('@/assets/p3/p3-tanleft.png')" :fit="cover"/>
                </el-col>
                <el-col :span="5" >
                    <span class="font1">继续?</span>
                </el-col>
            </el-row>
        </el-header>
        <el-container>
            <el-aside width="50px" >
                <el-row align="bottom" style="height:40%">
                    <el-col :span="24">
                        <el-image :src="require('@/assets/p3/p3-tan.png')" :fit="cover" />
                    </el-col>
                </el-row>
                <el-row type="flex" align="top" style="height:60%" >
                    <el-col>
                        <span class="font2">个人速写</span>
                    </el-col>
                </el-row>
            </el-aside>
            <el-main style="--el-main-padding: 5px">
                <el-row type="flex" justify="center" >
                        <el-col :xs="21" :sm="20" :md="12" :lg="12">
                            <el-form :model="form"  :rules="rules" ref="form" :size="large">
                                <el-form-item  prop="personality" label="性格特点概括(30字以内)"  style="margin-bottom:20px">
                                    <el-input 
                                    v-model="form.personality" 
                                    type="textarea" 
                                    :autosize="{minRows:4,maxRows:8}"
                                    maxlength="30"
                                    show-word-limit
                                    style="font-size:14px"
                                    ></el-input>
                                </el-form-item>
                                <el-form-item prop="skill" label="你的特长是？(多选)" style="margin-bottom:30px">
                                    <el-checkbox-group v-model="form.skill" >
                                        <el-row :gutter="20" type="flex" justify="start" >
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[0]"  >
                                                <el-checkbox label="唱歌" @change="changestyle(0)" >唱歌</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" :push="2" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[1]" >
                                                <el-checkbox label="舞蹈" @change="changestyle(1)">舞蹈</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" :push="5" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[2]">
                                                <el-checkbox label="书法" @change="changestyle(2)">书法</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction"  :class="class_style[3]">
                                                <el-checkbox label="摄影" @change="changestyle(3)">摄影</el-checkbox>
                                            </el-col>
                                            <el-col :span="6"  :push="2" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[4]">
                                                <el-checkbox label="主持" @change="changestyle(4)">主持</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" :push="5" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[5]">
                                                <el-checkbox label="乐器" @change="changestyle(5)">乐器</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[6]">
                                                <el-checkbox label="体育" @change="changestyle(6)">体育</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" :push="2" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[7]">
                                                <el-checkbox label="推文制作" @change="changestyle(7)">推文制作</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[8]">
                                                <el-checkbox label="文案" @change="changestyle(8)">文案</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" :push="2" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[9]">
                                                <el-checkbox label="网页制作" @change="changestyle(9)">网页制作</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[10]">
                                                <el-checkbox label="绘画" @change="changestyle(10)">绘画</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" :push="2" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[11]">
                                                <el-checkbox label="图像处理" @change="changestyle(11)">图像处理</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[12]">
                                                <el-checkbox label="活动策划" @change="changestyle(12)">活动策划</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" :push="2" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[13]">
                                                <el-checkbox label="音视频制作" @change="changestyle(13)">音视频制作</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" :push="5" style="display:flex;justify-content:flex-start;flex-direction" :class="class_style[14]">
                                                <el-checkbox label="其他"  @click="addskills">{{form.others}}</el-checkbox>
                                            </el-col>
                                        </el-row>
                                    </el-checkbox-group>
                                </el-form-item>
                                <el-form-item  prop="abilities" label="请从以下能力选择两个优势点" >
                                    <el-checkbox-group v-model="form.abilities" :max="2" >
                                        <el-row :gutter="8" style="margin-bottom:10px;margin-top: 10px;width:100%">
                                            <el-col :span="8">
                                                <el-checkbox-button label="凝聚力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                凝聚力
                                                </el-checkbox-button>
                                            </el-col>
                                            <el-col :span="8">
                                                <el-checkbox-button label="亲和力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                亲和力
                                                </el-checkbox-button>
                                            </el-col>
                                            <el-col :span="8">
                                                <el-checkbox-button label="领导力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                领导力
                                                </el-checkbox-button>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="8">
                                            <el-col :span="8">
                                                <el-checkbox-button label="创新力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                创新力
                                                </el-checkbox-button>
                                            </el-col>
                                            <el-col :span="8">
                                                <el-checkbox-button label="沟通力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                沟通力
                                                </el-checkbox-button>
                                            </el-col>
                                            <el-col :span="8">
                                                <el-checkbox-button label="适应力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                适应力
                                                </el-checkbox-button>
                                            </el-col>
                                        </el-row>
                                    </el-checkbox-group>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </el-row>
            </el-main>
        </el-container>
    </el-container>
    
  </div>
</template>

<script>
import { Form } from "../store/index";
export default {
  name: 'PersonPage',
  setup() {
    const PersonStore=Form()
    return {PersonStore}
  },
  data() {
    return {
        rules: {
            personality: [{
                required: true,
                message: '请输入你的答案',
                trigger: 'blur',
                }],
            skill:[{
                type:'array',
                required: true,
                message: '请至少选择一个特长',
                trigger: 'blur',
            }],
            abilities:[{
                type:'array',
                required: true,
                message: '请输入你的答案',
                trigger: 'blur',
            }]
        }
    }
  },
  computed:{
    form:{
        get(){
            return this.PersonStore.$state.person
        }
    },
    class_style:{
        get(){
            return this.PersonStore.$state.class_style
        }
    }
  },
  methods: {
    changestyle(value){
        if(this.class_style[value] === "check") {
              this.class_style[value] = "checked"
              this.PersonStore.checked(value)
        }
        else {
            this.class_style[value] = "check"
            this.PersonStore.discheck(value)
        }
    },
    next(){
        this.PersonStore.updateperson(this.form)
        this.$refs['form'].validate((valid)=>{
            if(valid){
                this.PersonStore.completeperson(true)
            }
            else  this.PersonStore.completeperson(false)
        })
        this.$router.push('/papic')
    },
    pre(){
        this.PersonStore.updateperson(this.form)
        this.$router.push('/basic')   
    },
    addskills(){
        if(this.form.oskill_checked){
                this.$prompt("请输入你其他的特长(5字以内)","其他",{
                confirmButtonText:"确定",
                showCancelButton:false,
                closeOnClickModal: false,
                closeOnPressEscape: false,
                closeOnHashChange: false,
                inputPlaceholder: "请输入你的其它特长哦",
                center:true,
                roundButton: true,
                inputPattern:/^.+$/,
                inputErrorMessage:"不能为空哦",
            })
            .then(({value})=>{
                this.form.others=value
                this.changestyle(14)
            })
            .catch(()=>{
                this.form.oskill_checked = false
                this.$message({
                    type:"info",
                    message:"取消输入"
                })
            })
        }
        else {
        this.form.oskill_checked=!this.form.oskill_checked
        if(this.form.oskill_checked){
            this.$prompt("请输入你其他的特长(5字以内)","其他",{
                confirmButtonText:"确定",
                showCancelButton:false,
                closeOnClickModal: false,
                closeOnPressEscape: false,
                closeOnHashChange: false,
                inputPlaceholder: "请输入你的其它特长哦",
                center:true,
                roundButton: true,
                inputPattern:/^.+$/,
                inputErrorMessage:"不能为空哦",
            })
            .then(({value})=>{
                this.form.others=value
                this.changestyle(14)
            })
            .catch(()=>{
                this.form.oskill_checked = false
                this.$message({
                    type:"info",
                    message:"取消输入"
                })
            })
        }
        else{
            this.form.others="其他"
        }
    }
    }
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.back{
  top: 0;
  left: 0;
  position: fixed;
  /* overflow: scroll; */
  background-color: black;
  background-image: url(../assets/p3/p3-2.png),url(../assets/p3/p3-1.jpeg);
  width: 100%;
  height: 100%;
  background-size: 100% 100%,100% auto;
  background-repeat: repeat,no-repeat;
}
.check{
    background-image: url(../assets/p3/p3-tan.png);
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    background-size: 45% auto;
    position: relative
}
.checked{
    background-image: url(../assets/p3/p3-light.png),url(../assets/p3/p3-tan.png);
    background-repeat: no-repeat,no-repeat;
    width: 100%;
    height: 100%;
    background-size: 45% auto,45% auto;
    position: relative
}
.font1{
    font-family: "papic";
    font-size: x-large;
    color: rgb(224, 224, 224);
}

.font2{
    font-family: "papic";
    font-size: xx-large;
    color: rgb(224, 224, 224);
}
</style>
