<script>
// // import { storeToRefs } from 'pinia'
// import { watch, ref } from "vue"
import { Form } from "../store/index"
export default {
  name: 'PapicPage',
  setup(){
    const PapicStore=Form()
    // let submit_checked=ref(false)
    // const {submit_checked}=storeToRefs(PapicStore)

    // PapicStore.$subscribe((mutation,state) =>{
    //   // console.log(state.submit_checked)
    //   if(state.submit_checked===true) submit_checked_2.value = true
    // },{detached: false})
    // watch(()=>PapicStore.submit_checked,(newV,oldV)=>{
    //     // console.log(oldV,newV,"111")
    //     submit_checked.value=true
    //     // console.log(submit_checked.value)
    // })
    return {
      PapicStore,
      // submit_checked
      }
  },
  data(){
    return{
      hello:true,
        rules: 
        {
          adjustment: [{
                required: true,
                message: '请选择是否接受调剂',
                trigger: 'blur',
                }],
          department1: [{
                required: true,
                message: '请至少选择一个部门',
                trigger: 'blur',
                }],
          reason: [{
                required: true,
                message: '请填写原因',
                trigger: 'blur',
                }], 
        },
        options:[
                  {
                     value:'办公室',
                     label:'办公室'
                  },
                  {
                     value:'人力资源部',
                     label:'人力资源部'
                  },
                  { 
                     value:'品牌公关部',
                     label:'品牌公关部'
                  },
                  {
                    value:'对外拓展部',
                    label:'对外拓展部'
                  },
                  {
                    value:'创业指导部',
                    label:'创业指导部'
                  },
                  {
                    value:'技术与视觉推广部',
                    label:'技术与视觉推广部',
                    children:[
                      {
                        value:'技术组',
                        label:'技术组'
                      },
                      {
                        value:'美工组',
                        label:'美工组'
                      }
                    ]
                  }
        ]
  }
 },
 computed:{
    form:{
        get(){
            return this.PapicStore.$state.papic
        }
    },
 },
 methods:{
    // chicken(){
    //   this.form.chicken_checked = !this.form.chicken_checked
    // },
    async submit(){
         this.PapicStore.updatepapic(this.form)
          this.$refs['form'].validate((valid)=>{
            if(valid){
                this.PapicStore.completepapic(true)
            }
            else this.PapicStore.completepapic(false)
        })
         this.$confirm("请确认消息是否填写完整！","",
         {
          confirmButtonText:"确认",
          cancelButtonText:"取消",
          type:'warning'
         })
         .then(()=>{
          this.PapicStore.submit()
          .then(res =>{
            if(res ==='success'){
             this.$message({
              message:'提交成功',
              type:'success',
              duration:4000
            })
            this.$router.push("/end")
            }
            else if(res==='no-complete'){
            this.$message({
              message:'请检查是否填写完毕',
              type:'error',
              })
            }
            else{
              this.$message({
                message: '提交失败，请重试',
                type: 'error'
              })
            }
         })
          // console.log(this.submit_checked)
          // if(res.data){
          //   console.log('hello')
          //   this.$message({
          //     message:'提交成功',
          //     type:'success',
          //     duration:4000
          //   })
          //   this.$router.push("/end")
          // }
          // else{
          //   this.$message({
          //     message:'提交失败，请重试',
          //     type:'error',
          //     duration:4000
          //   })
          // }
         })
         .catch(()=>{
          this.$message({
            message:'已取消提交',
            type:'info',
            duration:2000
          })
         })
    },
    pre(){
      this.PapicStore.updatepapic(this.form)
      this.$router.push('/person')
    }
 },

//  mounted() {
//   console.log(this.PapicStore.papic)
//  },
