<template>
 <transition :name="sliderName">
  <router-view></router-view>
 </transition>
</template>

<script>

export default {
  name: 'App',
  data(){
    return {
      sliderName:''
    }
  },
  watch:{
    $route(to,from){
      if(to.meta.index>from.meta.index){
        this.sliderName = 'slider-down'
      }
      else {
        this.sliderName = 'slider-up'
      }
    }
  }
}
</script>

