import axios from 'axios'
import {defineStore} from 'pinia'
import moment from 'moment'
// import { reject } from 'core-js/fn/promise'

export const Form = defineStore('form',{
    state: ()=>{
        return{
            basic:{
                name: "",
                sex: "",
                origin: "",
                birthday: "",
                major: "",
                class: "",
                dorm: "",
                number: "",
                phone: "",
                qq: "",
                mail: "",
            },
            person:{
                abilities: [],
                skill: [],
                personality: "",
                others: "其他",
                oskill_checked: false,
            },
            papic:{
                department1:'',
                department2:'',
                adjustment: "",
                reason: "",
                want: "",
                // chicken_checked:false
            },
            complete_basic:false,
            complete_person:false,
            complete_papic:false,
            class_style:["check","check","check","check","check","check","check","check","check","check","check","check","check","check","check"],
            // submit_checked:false
        }
    },
    getters:{

    },
    actions:{
    checked(newV){
        this.class_style[newV]='checked'
    },
    discheck(newV){
        this.class_style[newV]='check'
    },
    updatebasic(newValue)
    {
      this.basic=newValue
    },
    updatepapic(newValue)
    {
        this.papic=newValue
    },
    updateperson(newValue)
    {
        this.person=newValue
    },
    completebasic(newV){
        this.complete_basic=newV
        // console.log(newV)
    },
    completeperson(newV){
        this.complete_person=newV
    },
    completepapic(newV){
        this.complete_papic=newV
    },
    async submit(){
        return new Promise((resolve,reject)=>{
            if(this.complete_basic && this.complete_papic && this.complete_person){
                resolve(true)
            }
            else reject(false)
        })
        .then(()=>{
        let form={}
        form.name = this.basic.name
        form.sex = this.basic.sex
        form.number = this.basic.number
        form.origin = this.basic.origin
        form.birthday = this.basic.birthday
        form.major = this.basic.major
        form.class = this.basic.class
		form.dorm = this.basic.dorm
		form.qq = this.basic.qq
		form.mail = this.basic.mail
		form.phone = this.basic.phone
		// form.photocheck = this.basic.photocheck
		form.personality = this.person.personality
		form.skill = this.person.skill
        if(this.person.oskill_checked) form.skill.push(this.person.others);
        form.abilities = this.person.abilities
        form.department = []
        form.department.push(this.papic.department1)
        form.department.push(this.papic.department2)
        form.adjustment = this.papic.adjustment
        form.reason = this.papic.reason
        form.want = this.papic.want
        form.birthday = moment(form.birthday).format('L');
		form.skill = JSON.stringify(form.skill);
		form.abilities = JSON.stringify(form.abilities);
		form.department = JSON.stringify(form.department)
       return new Promise((resolve,reject)=>{
        axios.post("/submit",form)
        .then(res =>{
            console.log(res.data)
            resolve(res.data)
        })
        .catch(e =>{
            reject(e)
            console.log(e)
        })
       })
 
        // .then((res) => {
        //         if(res.data === 'success') {
        //             this.submit_checked=true
        //         }
        //         // console.log(this.submit_checked)
        // })
        // .catch((e)=>{
        //         console.log(e)
        //     })
    })
    .catch(()=>{
        return 'no-complete'
    })
}
 
}})
